import React from 'react';
import { Link } from 'react-router-dom';
export default function Navbar() {
    return (

        <nav className="navbar navbar-expand-lg navbar-light" id="navbar" style={{backgroundColor: " rgb(75 77 77)"}}>
            <div className="container-fluid">
                <Link className="navbar-brand" to="/" style={{color: "#b5bfc0"}}>Daily Buzz</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="/navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className="nav-link"  style={{color: "#b5bfc0"}} aria-current="page" to="/">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/business">Business</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/health">Health</Link>
                        </li> 
                        <li className="nav-item">
                            <Link className="nav-link" to="/science">Science</Link>
                        </li>
                         <li className="nav-item">
                            <Link className="nav-link" to="/sports">Sports</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/technology">Technology</Link>
                        </li>
                      
                        
                       
                    </ul>
                   
                </div>
            </div>
        </nav>

    )
}
